}

.container {
  height: auto;
  width: 100%;
}

/*page transition*/
.page {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms linear 150ms;
  transition: opacity 300ms linear 150ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 150ms linear;
  transition: opacity 150ms linear;
}

.transition-group {
  height: 100%;
}

.triangle-one {
	width: 0;
	height: 0;
	-webkit-transform: rotate(33deg);
	        transform: rotate(33deg);
	top: -68px;
	left: -187px;
	position: absolute;
	border-left   : 165px solid transparent;
	border-right  : 300px solid transparent;
	border-bottom : 170px solid rgba(128, 128, 128, 0.35);
}

.triangle-one::before {
	content: "";
	width: 0;
	height: 0;
	position: absolute;
	top: 4px;
	left: -157px;
	border-left: 157px solid transparent;
	border-right: 293px solid transparent;
	border-bottom: 163px solid black;
}

.triangle-two {
	width: 0;
	height: 0;
	-webkit-transform: rotate(-141deg);
	        transform: rotate(-141deg);
	top: 268px;
	left: -392px;
	position: absolute;
	border-left: 139px solid transparent;
	border-right: 181px solid transparent;
	border-bottom: 134px solid rgba(128, 128, 128, 0.35);
}

.triangle-two::before {
	content: "";
	width: 0;
	height: 0;
	top: 4px;
	left: -130px;
	position: absolute;
	border-left: 129px solid transparent;
	border-right: 170px solid transparent;
	border-bottom: 126px solid black;
}

.triangle-three {
	width: 0;
	height: 0;
	-webkit-transform: rotate(9deg);
	        transform: rotate(9deg);
	top: 272px;
	left: 227px;
	position: absolute;
	border-left: 165px solid transparent;
	border-right: 300px solid transparent;
	border-bottom: 170px solid rgba(128, 128, 128, 0.35);
}

.triangle-three::before {
	content: "";
	width: 0;
	height: 0;
	top: 3px;
	left: -163px;
	position: absolute;
	border-left: 163px solid transparent;
	border-right: 287px solid transparent;
	border-bottom: 164px solid black;
}

.triangle-four {
	width: 0;
	height: 0;
	-webkit-transform: rotate(-76deg);
	        transform: rotate(-76deg);
	top: -38px;
	left: 577px;
	position: absolute;
	border-left: 49px solid transparent;
	border-right: 191px solid transparent;
	border-bottom: 99px solid rgba(128, 128, 128, 0.35);
}

.triangle-four::before {
	content: "";
	width: 0;
	height: 0;
	top: 4px;
	left: -44px;
	position: absolute;
	border-left: 45px solid transparent;
	border-right: 177px solid transparent;
	border-bottom: 92px solid black;
}

.line-one {
	border: 1px solid #0cd0f2;
	width: 778px;
	-webkit-transform: rotate(-34deg);
	        transform: rotate(-34deg);
	position: absolute;
	top: 191px;
	left: -131px;
}

.line-two {
	border: 1px solid #ffc722;
	width: 644px;
	-webkit-transform: rotate(-55deg);
	        transform: rotate(-55deg);
	position: absolute;
	top: 191px;
	left: -107px;
}

.line-three {
	border: 1px solid #467fd7;
	width: 1103px;
	-webkit-transform: rotate(13deg);
	        transform: rotate(13deg);
	position: absolute;
	top: 186px;
	left: -307px;
}

.middle-shape {
	width: 35%;
	height: 500px;
	text-align: center;
	margin: 0 auto 0 auto;
