}

.diamond-shape {
	width: 385px;
	height: 385px;
	background-color: black;
	position: absolute;
	border: 5px solid #ffc722;
	left: 50%;
	margin-left: -192px;
	z-index: 10;
}

.diamond-shape::after {
	content: "";
	width: 327px;
	height: 327px;
	background-color: black;
	position: absolute;
	top: 25px;
	left: 25px;
	border: 3px solid #ffc722;
	z-index: -1;
}

.greeting-text {
	color: white;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	position: absolute;
	top: 95px;
	left: 72px;
	font-size: 30px;
	margin: 0;
}

.diamond-shape h2 {
	color: #03a8f0;
	position: absolute;
	font-size: 40px;
	top: 106px;
	left: 28px;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

.home-description {
	color: white;
	font-style: italic;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	position: absolute;
	top: 179px;
	left: 95px;
}

.square-shape {
	width: 385px;
	height: 385px;
	background-color: black;
	position: absolute;
	border: 5px solid #177DEB;
	left: 50%;
	margin-left: -192px;
	background-image: url(/static/media/star_background.6d8fc611.gif);
}

/*rotate diamond shape*/
.rotate-first {
	 transform: rotate(45deg);
	 -webkit-transform: rotate(45deg);
	 -moz-transform: rotate(45deg);
	 -ms-transform: rotate(45deg);
	 -o-transform: rotate(45deg);
 }

