.overlay {
	z-index: 1;
	position: absolute;
	left: 0;
	right: 0;
	overflow: hidden;
	width: 100%;
	height: 50%;
	-webkit-transition: .9s ease;
	transition: .9s ease;
}

.overlay img {
	width: 100%;
	height: 100%;
	-webkit-transition: -webkit-transform .5s;
	transition: -webkit-transform .5s;
	transition: transform .5s;
	transition: transform .5s, -webkit-transform .5s;
}

